<template>
  <div class="imagesizes">
    <div class="header">
      <div class="change-sizes">
        <input
          class="change-sizes-input"
          @focus="onFocus = 'width'"
          type="text"
          v-model.number.lazy="width"
        />
        <div class="wrapper-ratio">
          <q-btn-toggle
            class="change-sizes-ratio"
            v-model="isRatio"
            :options="ratioOptions"
            clearable
            toggle-color="primary"
            :disable="isDisableRatio"
          />
        </div>
        <input
          class="change-sizes-input"
          @focus="onFocus = 'height'"
          type="text"
          v-model.number.lazy="height"
        />
      </div>
      <div class="main">
        <q-btn
          class="main-btn"
          title="Обновить изображения"
          @click="getFiles"
          flat
          round
          color="primary"
          icon="refresh"
        />
        <q-btn
          class="main-btn"
          :color="btnColorCreateImageSize"
          @click="createImageSize"
          label="Изменить размер"
          :icon-right="btnIconCreateImageSize"
          :loading="isCreateImageSize"
          :disable="isCreateImageSize"
        >
          <template v-slot:loading>
            <q-spinner-tail size="sm" />
          </template>
        </q-btn>
      </div>
    </div>
    <div class="checked">
      <q-checkbox v-model="checkFiles" />
    </div>
    <div class="images">
      <q-card class="item" v-for="image of arrImages" :key="image.name">
        <q-card-section>
          <div class="top">
            <p class="name" :title="image.name">{{ image.name }}</p>
            <q-checkbox class="check-ready" v-model="image.isReady" />
          </div>

          <div class="size width">
            <p class="size-name">Ширина:</p>
            <input class="edit edit-width" type="text" v-model.number="image.sizes.width" />
          </div>
          <div class="size height">
            <p class="size-name">Высота:</p>
            <input class="edit edit-height" type="text" v-model.number="image.sizes.height" />
          </div>
        </q-card-section>
      </q-card>
    </div>
  </div>
</template>

 <script>
import { mapState, mapMutations } from 'vuex';
import { saveBtn } from '@/mixins/saving';

export default {
  name: 'ImageSizes',
  mixins: [saveBtn],
  data: function () {
    return {
      isCreateImageSize: false,
      btnColorCreateImageSize: 'primary',
      btnIconCreateImageSize: 'transform',
      arrImages: [],
      checkFiles: true,
      width: 0,
      height: 0,
      onFocus: '',
      isDisableRatio: false,
      isRatio: null,
      ratioOptions: [{ label: '×', value: true }],
    };
  },
  computed: {
    ...mapState({
      baseURL: (state) => state.baseURL,
      globalOptions: (state) => state.settings.options,
    }),
  },
  watch: {
    width(val, oldVal) {
      if (this.isRatio && this.onFocus === 'width') {
        this.arrImages.forEach((item) => (item.sizes.width = val));
        const valHeight = (val * this.height) / oldVal;
        this.height = Math.floor(valHeight);
      } else {
        this.arrImages.forEach((item) => (item.sizes.width = val));
      }
    },
    height(val, oldVal) {
      if (this.isRatio && this.onFocus === 'height') {
        this.arrImages.forEach((item) => (item.sizes.height = val));
        const valWidth = (val * this.width) / oldVal;
        this.width = Math.floor(valWidth);
      } else {
        this.arrImages.forEach((item) => (item.sizes.height = val));
      }
    },
    checkFiles(val) {
      this.arrImages.forEach((item) => (item.isReady = val));
    },
    isRatio(val) {
      this.globalOptions.helpers.isRatio = val;
      this.setGlobalOptions();
    },
  },
  methods: {
    ...mapMutations(['setGlobalOptions']),
    async createImageSize() {
      this.isCreateImageSize = true;
      let arrCreateImages = this.arrImages.filter((item) => item.isReady);

      let response, result;
      try {
        response = await fetch(this.baseURL + 'helpers_change_image_sizes', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json;charset=utf-8',
          },
          body: JSON.stringify({ arrImages: arrCreateImages }),
        });
        result = await response.json();

        if (result.error && result.error !== '') {
          this.saveAction(
            'btnIconCreateImageSize',
            'btnColorCreateImageSize',
            false
          ); //mixin
          console.error(result.error);
        } else {
          this.saveAction('btnIconCreateImageSize', 'btnColorCreateImageSize'); //mixin
        }
      } catch (error) {
        this.saveAction(
          'btnIconCreateImageSize',
          'btnColorCreateImageSize',
          false
        ); //mixin
        console.error(error);
      }

      this.isCreateImageSize = false;
    },
    async getFiles() {
      let response, result;
      try {
        response = await fetch(this.baseURL + 'helpers_image_sizes');
        result = await response.json();
      } catch (error) {
        console.error('getFiles', error);
      }
      this.checkSameSazes(result);

      this.arrImages = result;
    },
    checkSameSazes(arrImages) {
      //  Проверяем если все размеры в массиве одинаковые то включаем соотношение сторон
      const fistWidth = arrImages[0].sizes.width;
      const fistHeight = arrImages[0].sizes.height;

      const isWidth = arrImages.every((item) => item.sizes.width === fistWidth);
      const isHeight = arrImages.every(
        (item) => item.sizes.height === fistHeight
      );

      if (isWidth && isHeight) {
        this.width = fistWidth;
        this.height = fistHeight;
        this.isDisableRatio = false;
      } else {
        this.isDisableRatio = true;
      }
    },
    getGlobalOptions() {
      this.isRatio = this.globalOptions.helpers.isRatio;
    },
  },
  mounted() {},
  created() {
    this.getGlobalOptions();
    this.getFiles();
  },
};
</script>

 <style  lang="scss" scoped>
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}

.header .main {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .main .main-btn {
  margin-left: 1rem;
}

.change-sizes {
  display: grid;
  grid-template-columns: 5rem 2rem 5rem;
  align-items: center;
  grid-column-gap: 0.5rem;
}

.change-sizes-input {
  border: none;
  outline: none;
  font-size: 180%;
  background: rgb(224, 224, 224);
}

.wrapper-ratio {
  display: flex;
  align-items: center;
  justify-content: center;
}

.change-sizes-ratio ::v-deep .q-btn__wrapper {
  padding: 0 0.8rem;
}

.images {
  display: flex;
  flex-wrap: wrap;
}

.images .item {
  margin-right: 1rem;
  margin-bottom: 1rem;
  flex-basis: 11rem;
}

.item .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  margin-top: -0.5rem;
}

.item .name {
  width: 7rem;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

.item .check-ready {
  margin-right: -0.5rem;
}

.item .size {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.item .size-name {
  font-size: 90%;
}

.item p {
  margin: 0;
}

.item .width {
  margin-bottom: 0.2rem;
}

.item .height {
  margin-bottom: 0;
}

.item .edit {
  width: 3rem;
  border: none;
  background-color: rgb(224, 224, 224);
  outline: none;
  margin-left: 0.2rem;
}

.checked {
  display: flex;
  padding-right: 1rem;
  margin-bottom: 1rem;
}
</style>